-right:15px;transition:transform .2s ease-in-out;width:14px;height:14px}.cartItem{border:1px solid #f3f3f3;background:#fff;border-radius:20px;overflow:hidden;padding:15px}.cartItem .cartItemImg{height:90px;width:90px;background-size:contain;background-position:center;background-repeat:no-repeat;margin-right:20px}.cartItem p{font-size:16px;margin:0}.cartItem b{font-size:14px}.cartItem .removeBtn{opacity:.5;transition:opacity .2s ease-in-out;cursor:pointer}.cartItem .removeBtn:hover{opacity:1}@keyframes button-loading{0%{opacity:1}50%{opacity:.8}100%{opacity:1}}